@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@700;800;900&display=swap');

@font-face {
  font-family: 'Midnight';
  src: url('./Midnight.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

html {
  scroll-behavior: smooth;
}

body {
  @apply bg-white text-gray-900 font-sans;
  background-image: url('./texture.png');
  background-repeat: repeat;
  background-size: 200px 200px;
}

code {
  @apply font-mono;
}

:root {
  --google-sans: 'Inter', ui-sans-serif, system-ui, -apple-system, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji';
  --midnight-font: 'Midnight', 'Inter', ui-sans-serif, system-ui;
}

.font-bold,
.font-extrabold,
.font-black,
b,
strong,
h1,
h2,
h3 {
  font-family: var(--midnight-font);
  letter-spacing: -0.02em;
}
